'use client';

import { useState } from 'react';
import { format, startOfMonth, endOfMonth, eachDayOfInterval, isSameDay, isSameMonth, addMonths, subMonths } from 'date-fns';
import { ChevronLeft, ChevronRight, CheckCircle2, ShieldAlert } from 'lucide-react';
import { Button } from '@/components/ui/button';
import type { Event } from '@/types';
import { cn } from '@/lib/utils';
import { Badge } from '../ui/badge';
import type { useMockDatabase } from '@/lib/data';

interface EventCalendarProps {
  events: Event[];
  onEventClick: (event: Event) => void;
  db: ReturnType<typeof useMockDatabase>;
}

export default function EventCalendar({ events, onEventClick, db }: EventCalendarProps) {
  const [currentMonth, setCurrentMonth] = useState(new Date());

  const days = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];

  const firstDayOfMonth = startOfMonth(currentMonth);
  const lastDayOfMonth = endOfMonth(currentMonth);

  const daysInMonth = eachDayOfInterval({
    start: firstDayOfMonth,
    end: lastDayOfMonth,
  });

  const startingDayIndex = firstDayOfMonth.getDay();

  const prevMonth = () => setCurrentMonth(subMonths(currentMonth, 1));
  const nextMonth = () => setCurrentMonth(addMonths(currentMonth, 1));

  const getEventsForDay = (day: Date) => {
    return events
      .filter(event => isSameDay(event.eventDate, day))
      .sort((a, b) => a.eventDate.getTime() - b.eventDate.getTime());
  };
  
  const currentMonthStr = `${currentMonth.getFullYear()}-${String(currentMonth.getMonth() + 1).padStart(2, '0')}`;
  const isPublished = db.isMonthPublished(currentMonthStr);
  
  const handlePublish = () => {
    db.publishMonth(currentMonthStr);
  }

  return (
    <div className="w-full bg-card text-card-foreground">
      <div className="flex items-center justify-between px-4 py-2 border-b flex-wrap gap-2">
        <h2 className="text-xl font-bold font-headline">
          {format(currentMonth, 'MMMM yyyy')}
        </h2>
        <div className="flex items-center gap-2">
          <Button variant="outline" size="icon" className="h-8 w-8" onClick={prevMonth}>
            <ChevronLeft className="h-4 w-4" />
          </Button>
          <Button variant="outline" size="icon" className="h-8 w-8" onClick={nextMonth}>
            <ChevronRight className="h-4 w-4" />
          </Button>
        </div>
         <div className="flex items-center gap-2">
            {isPublished ? (
                <div className="flex items-center gap-2 text-sm font-medium text-green-600 p-2 rounded-md bg-green-100">
                    <CheckCircle2 className="h-5 w-5" />
                    <span>Month Published</span>
                </div>
            ) : (
                 <Button onClick={handlePublish}>
                    <ShieldAlert className="mr-2 h-4 w-4" />
                    Publish Month
                </Button>
            )}
        </div>
      </div>
      <div className="grid grid-cols-7 w-full">
        {days.map(day => (
          <div key={day} className="text-center font-semibold text-muted-foreground p-2 border-b">
            {day}
          </div>
        ))}
      </div>
      <div className="grid grid-cols-7 grid-rows-5 w-full">
        {Array.from({ length: startingDayIndex }).map((_, index) => (
          <div key={`empty-${index}`} className="border-r border-b" />
        ))}
        {daysInMonth.map(day => {
          const dayEvents = getEventsForDay(day);
          return (
            <div
              key={day.toString()}
              className={cn(
                'min-h-[120px] p-2 border-r border-b flex flex-col',
                isSameDay(day, new Date()) && 'bg-accent/20'
              )}
            >
              <time dateTime={format(day, 'yyyy-MM-dd')} className={cn("font-semibold", !isSameMonth(day, currentMonth) ? "text-muted-foreground" : "")}>
                {format(day, 'd')}
              </time>
              <div className="flex-grow mt-1 space-y-1 overflow-y-auto">
                {dayEvents.map(event => (
                  <div
                    key={event.id}
                    onClick={() => onEventClick(event)}
                    className="p-1.5 rounded-md bg-primary/10 hover:bg-primary/20 cursor-pointer"
                  >
                    <p className="text-xs font-semibold truncate text-primary/90">{event.eventName}</p>
                    <p className="text-xs text-muted-foreground">{format(event.eventDate, 'p')}</p>
                  </div>
                ))}
              </div>
            </div>
          );
        })}
        {Array.from({ length: 42 - daysInMonth.length - startingDayIndex }).map((_, index) => (
           <div key={`empty-end-${index}`} className="border-r border-b" />
        ))}
      </div>
    </div>
  );
}
