'use client';
import { useState, useEffect } from 'react';
import { useForm, useFieldArray } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Loader2, Plus, Trash2, X } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from '@/components/ui/dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useToast } from '@/hooks/use-toast';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useMockDatabase } from '@/lib/data';
import type { UniversalRole, TemplateRole } from '@/types';
import { Badge } from '../ui/badge';

const templateFormSchema = z.object({
  name: z.string().min(3, 'Template name must be at least 3 characters.'),
  roles: z.array(z.object({
    roleId: z.string(),
    name: z.string(),
  })).min(1, 'A template must have at least one role.'),
});

type TemplateFormValues = z.infer<typeof templateFormSchema>;

interface CreateTemplateModalProps {
  isOpen: boolean;
  onClose: () => void;
  db: ReturnType<typeof useMockDatabase>;
}

export default function CreateTemplateModal({ isOpen, onClose, db }: CreateTemplateModalProps) {
  const { toast } = useToast();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [universalRoles, setUniversalRoles] = useState<UniversalRole[]>([]);

  const form = useForm<TemplateFormValues>({
    resolver: zodResolver(templateFormSchema),
    defaultValues: {
      name: '',
      roles: [],
    },
  });

  const { fields, append, remove } = useFieldArray({
    control: form.control,
    name: 'roles',
  });

  useEffect(() => {
    if (isOpen) {
      db.getUniversalRoles().then(setUniversalRoles);
      form.reset({
        name: '',
        roles: [],
      });
    }
  }, [isOpen, db, form]);
  
  const addRole = (roleId: string) => {
    const role = universalRoles.find(r => r.id === roleId);
    if (role) {
        append({ roleId: role.id, name: role.name });
    }
  }

  const onSubmit = async (data: TemplateFormValues) => {
    setIsSubmitting(true);
    try {
        const templateRoles: TemplateRole[] = data.roles.map(r => ({
            ...r,
            instanceId: `role_${Date.now()}_${Math.random()}`,
        }));

      await db.createServiceTemplate(data.name, templateRoles);

      toast({
        title: 'Template Created',
        description: `"${data.name}" has been saved.`,
      });
      form.reset();
      onClose();
    } catch (error) {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: 'Failed to create template. Please try again.',
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[480px]">
        <DialogHeader>
          <DialogTitle className="font-headline">Create New Template</DialogTitle>
          <DialogDescription>Create a reusable template for your services.</DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 py-4">
            <FormField
              control={form.control}
              name="name"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Template Name</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g., Standard Sunday Service" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            
            <FormItem>
                <FormLabel>Roles</FormLabel>
                <div className="flex flex-wrap gap-2 p-2 rounded-md border min-h-12 bg-background">
                    {fields.map((field, index) => (
                        <Badge key={field.id} variant="secondary" className="flex items-center gap-1">
                            <span>{field.name}</span>
                            <button type="button" onClick={() => remove(index)} className="ml-1 rounded-full hover:bg-muted-foreground/20">
                                <X className="h-3 w-3" />
                            </button>
                        </Badge>
                    ))}
                    {fields.length === 0 && <p className="text-sm text-muted-foreground px-2">Add roles below...</p>}
                </div>
                 <FormField
                    control={form.control}
                    name="roles"
                    render={() => (
                        <FormMessage className="mt-2" />
                     )}
                />
            </FormItem>

             <Select onValueChange={addRole} value="">
                <SelectTrigger>
                    <SelectValue placeholder="Add a role to the template..." />
                </SelectTrigger>
                <SelectContent>
                    {universalRoles.map(role => (
                        <SelectItem key={role.id} value={role.id}>{role.name}</SelectItem>
                    ))}
                </SelectContent>
            </Select>

            <DialogFooter>
              <Button type="button" variant="ghost" onClick={onClose} disabled={isSubmitting}>Cancel</Button>
              <Button type="submit" disabled={isSubmitting}>
                {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                Create Template
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
