'use server';
/**
 * @fileOverview Fetches and parses ICS calendar content from a URL.
 * 
 * - fetchAndParseIcs - A function that handles fetching and parsing of an ICS URL.
 */

import { ai } from '@/ai/genkit';
import { z } from 'zod';
import { parseIcsContent } from './parse-ics-flow';
import type { ParsedEvent } from '@/types';

export async function fetchAndParseIcs(url: string): Promise<ParsedEvent[]> {
    const fetchIcsFlow = ai.defineFlow(
        {
            name: 'fetchIcsFromUrlFlow',
            inputSchema: z.string().url(),
            outputSchema: z.array(z.any()),
        },
        async (icsUrl) => {
            try {
                const response = await fetch(icsUrl);
                if (!response.ok) {
                    throw new Error(`Failed to fetch ICS file: ${response.statusText}`);
                }
                const icsContent = await response.text();
                return await parseIcsContent(icsContent);
            } catch (error: any) {
                console.error("Error fetching or parsing ICS from URL:", error);
                throw new Error("Could not retrieve or parse the calendar data from the provided URL.");
            }
        }
    );

    return await fetchIcsFlow(url);
}
